import React from 'react'

const TrainingMax = ({ name, week, trainingMax, setAppState }) =>
  <input type="text" 
    className="training-max" 
    value={week === 'I' ? trainingMax : ''}
    onChange={e => {
      if (isNaN(e.target.value)) return
      const nextState = parseInt(e.target.value, 10)
      switch (name) {
        case 'press': setAppState({ press: nextState }); break
        case 'deadlift': setAppState({ deadlift: nextState }); break
        case 'bench': setAppState({ bench: nextState }); break
        case 'squat': setAppState({ squat: nextState }); break
        default: break
      }
    }}
  />

export default TrainingMax


